.header {
    background-color: #0b1e1e;
  }
  
  .nav-list {
    display: flex;
    gap: 30px;
  }
  
  .nav-link {
    display: block;
    padding: 32px 0;
    color: #e9c1c1;
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 0.02em;
    text-decoration: none;
  
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .nav-link:hover,
  .nav-link:focus {
    color: #9b132d;
  }
  
  .active {
    composes: nav-link;
    color: #9b132d;
  }