.navLink-list {
    margin-top: 30px;
  }
  .nav-link {
    display: block;
    margin-bottom: 5px;
    color: #0b1e1e;
  
    font-weight: 700;
    line-height: 1.14;
    letter-spacing: 0.02em;
    text-decoration: none;
  
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .nav-link:hover,
  .nav-link:focus {
    color: #9b132d;
  }
  
  .active {
    composes: nav-link;
    color: #9b132d;
  }